export default {
	data() {
		return {
			news : []
		}
	},
	onLoad() {
		uni.request({
			url: 'https://unidemo.dcloud.net.cn/api/news',
			method: 'GET',
			data: {},
			success: res => {
				console.log(res);
				this.news = res.data;
			},
			fail: () => {},
			complete: () => {}
		});
	},
	methods: {
		openinfo(e){
			console.log(e);
			uni.navigateTo({
				url: '../info/info?postid='+e.currentTarget.dataset.newsid,
			});
		}

	}
}
